* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: #0059E7;
}

a {
    color: unset;
    text-decoration: none;
  }

/* GRADIENT STYLING - Generated using angrytools.com/gradient/    */

.gradient__bg {
    background:-moz-radial-gradient(circle at 3% 25%, rgb(14, 210, 63) 0%, #0059E7(4, 12, 24, 1) 25%);
  
    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 3% 25%, rgb(83, 64, 0) 0%, #0059E7(4, 12, 24, 1) 25%);
  
    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 3% 25%, rgb(83, 64, 0) 0%, #0059E7(4, 12, 24, 1) 25%);
  
    /* ie 10+ */
    background:-ms-radial-gradient(circle at 3% 25%, rgb(0, 83, 3) 0%, #0059E7(4, 12, 24, 1) 25%);
  
    /* global 92%+ browsers support */
    background:radial-gradient(circle at 3% 25%, rgb(0, 255, 47) 0%, #0059E7(4, 12, 24, 1) 25%);
  }
  
  .gradient__text {
    background:-moz-radial-gradient(circle at 3% 25%, rgb(14, 210, 63) 0%, #0059E7(4, 12, 24, 1) 25%);
  
    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 3% 25%, rgb(83, 64, 0) 0%, #0059E7(4, 12, 24, 1) 25%);
  
    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 3% 25%, rgb(83, 64, 0) 0%, #0059E7(4, 12, 24, 1) 25%);
  
    /* ie 10+ */
    background:-ms-radial-gradient(circle at 3% 25%, rgb(0, 83, 3) 0%, #0059E7(4, 12, 24, 1) 25%);
  
    /* global 92%+ browsers support */
    background:radial-gradient(circle at 3% 25%, rgb(0, 255, 47) 0%, #0059E7(4, 12, 24, 1) 25%);
  }
  
  .section-padding {
    padding: 4rem 6rem;
  }
  
  .section-margin {
    margin: 4rem 6rem;
  }
  
/* ANIMATIONS - generated using animista.net */
  
  .scale-up-center {
      -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
      animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  }
  
  @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
/* MEDIA QUERIES STYLING */

  @media screen and (max-width: 700px) {
    .section-padding {
      padding: 4rem;
    }
  
    .section-margin {
      margin: 4rem;
    }
  }
  
  @media screen and (max-width: 550px) {
    .section-padding {
      padding: 4rem 2rem;
    }
  
    .section-margin {
      margin: 4rem 2rem;
    }  
  }